.profile-background {
    /* height: 370px; */
    height: 53vh;
    height: 53svh;
}

.profile-grid {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
    padding: 0;
    margin: 0;
}

.profile-grid .grid-item {
    box-sizing: border-box;
    position: relative;
}

.profile-grid .grid-item:first-child {
    flex-basis: 100px;
}

.profile-grid .grid-item:not(:first-child) {
    flex-basis: auto;
    padding: 0;
}

.profile_details-list {
    list-style: none;
    margin: 8px 0 8px 32px;
    padding: 0;
}

.profile_details-list li {
    padding: 0.25em 0;
}

.profile_details-username {
    color: #344767;
    font-weight: 600;
    margin-right: 30px;
    padding-bottom: 0.10em !important;
}

.profile_details-rank {
    color: #7B809A;
    font-weight: 300;
}

.profile_details-time {
    color: #7b809a7c;
    font-weight: 300;
}