.spinner-container {
  display: flex;
  justify-content: center;
  align-items: center;
}

.spinner {
  border: 6px solid;
  border-color: #344767 transparent #344767 transparent;
  border-radius: 50%;
  animation: spin 1.3s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}